@import "./src/sass/helpers/var";

.spinner {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  overflow: hidden;

  &--wrapper {
    border: 2px dashed $purple;
    position: absolute;
    min-height: 92.5vh;
    width: 50%;
    left: 0;
    transform: translateX(-50%);
    margin: auto 0;
    border-radius: 50%; } }

@media screen and ( max-width: 1600px) {
  .spinner {
    &--wrapper {
      min-height: 95%; } } }

@media screen and ( max-width: 1366px) {
  .spinner {
    &--wrapper {
      min-height: 65%;
      width: 40%; } } }

@media screen and ( max-width: 1024px) {
  .spinner {
    &--wrapper {
      min-height: 55%;
      width: 55%; } } }

@media screen and (max-width: 768px) {
  .spinner {
    &--wrapper {
      width: 65%;
      min-height: 50%; } } }

@media screen and (max-width: 480px) {
  .spinner {
    &--wrapper {
      width: 25%;
      min-height: 15%; } } }
