@import "./src/sass/helpers/var";

.services-box {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  grid-gap: 2.5rem;

  &__item {
    display: flex;
    flex-direction: column;
    padding: 10%;
    width: 100%;
    background-color: $white;
    transition: $transition;

    h3 {
      font-size: 1.75vw;
      width: 100%;
      text-align: left; }

    p {
      width: 100%;
      text-align: justify;
      font-size: 0.8vw;
      padding: 1.25rem 0; }

    &:hover,
    &:focus,
    &:active {
      box-shadow: 0 0 30px 0 rgba($purple, 0.5); } } }

@media screen and (max-width: 1224px) {
  .services-box {
    &__item {
      h3 {
        font-size: 2vw; }
      p {
        font-size: 1.1vw; } } } }

@media screen and (max-width: 1024px) {
  .services-box {
    grid-template-columns: repeat(2, auto);

    &__item {
      h3 {
        font-size: 3vw; }
      p {
        font-size: 1.5vw; } } } }

@media screen and (max-width: 480px) {
  .services-box {
    grid-template-columns: repeat(1, auto);

    &__item {
      h3 {
        font-size: 6.5vw; }
      p {
        font-size: 3vw; } } } }
