@import "./src/sass/helpers/var";

.blog-box {
  display: grid;
  width: 100%;
  padding: 2.5% 0;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2.5rem;

  &__item {
    display: flex;
    flex-direction: column;
    background-color: $white;
    transition: $transition;
    position: relative;
    width: 100%;
    margin: auto;
    padding: 10%;

    h3 {
      font-size: 1.5vw;
      width: 100%;
      text-align: justify;
      position: absolute;
      bottom: 15%;
      left: 0;
      padding: 0 10%;
      color: $white; }

    &:hover,
    &:focus,
    &:active {
      box-shadow: 0 0 30px 0 rgba($purple, 0.5); } }

  &__img {
    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba($black, 0.25); } } }

.date-publish {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  padding: 5% 10%;
  display: flex;
  line-height: 1.25rem;
  font-size: 0.8vw;

  &__number {
    margin-right: 0.5rem;
    display: block;
    line-height: 2.5rem;
    font-size: 2.25vw; } }

.categories {

  width: 100%;
  padding: 1.25rem 0;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  left: 0;
  padding: 5% 10%;
  display: flex;
  width: 100%;
  align-items: center;
  flex-wrap: wrap;

  &__item {
    display: flex;
    padding: 0.5rem 0.75rem;
    background-color: rgba($black, 0.5);
    margin-right: 0.5rem;
    color: $white;
    font-size: 0.65vw; } }

@media screen and (max-width: 1600px) {
  .blog-box {
    &__item {
      h3 {
        font-size: 1.75vw; } } }

  .categories {
    &__item {
      font-size: 0.75vw; } } }

@media screen and (max-width: 1366px) {
  .blog-box {
    &__item {
      padding: 7.5%; } } }

@media screen and (max-width: 1224px) {
  .blog-box {
    &__item {
      h3 {
        font-size: 2vw; } } }

  .date-publish {
    font-size: 1vw;
    &__number {
      font-size: 3vw; } } }

@media screen and (max-width: 1024px) {
  .blog-box {
    grid-template-columns: repeat(2, auto);
    padding: 10% 0;

    &__item {
      h3 {
        font-size: 3.5vw; } } }

  .date-publish {
    font-size: 2vw;
    &__number {
      font-size: 5vw; } }

  .categories {
    &__item {
      font-size: 1.5vw; } } }

@media screen and (max-width: 480px) {
  .blog-box {
    grid-template-columns: repeat(1, auto);
    &__item {
      h3 {
        font-size: 6.5vw; } } }

  .date-publish {
    font-size: 3.5vw;
    &__number {
      font-size: 9vw; } }

  .categories {
    &__item {
      font-size: 2.5vw; } } }
