@import "./src/sass/helpers/var";

.project {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;

  &__image {
    width: 95%;
    object-fit: cover;
    background-color: $white;
    transition: $transition;

    img {
      object-fit: cover;
      width: 100%; } }

  &__title {
    font-size: 1.5vw;
    width: 100%;
    padding: 2rem 0;
    transition: $transition; }

  &__caption {
    display: flex;
    width: 100%;
    transition: $transition;
    font-size: 0.75vw;
    &:hover,
    &:focus,
    &:active {
      color: $purple; } } }

.projects--wrapper {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(4, auto);
  padding: 0;
  grid-gap: 2.5%; }

@media screen and ( max-width: 1366px) {
  .project {
    &__title {
      font-size: 2vw; }
    &__caption {
      font-size: 1.1vw; } } }

@media screen and ( max-width: 1024px) {
  .project {
    &__title {
      font-size: 3vw; }
    &__caption {
      font-size: 1.75vw; } } }

@media screen and ( max-width: 768px) {
  .project {
    &__title {
      font-size: 3.25vw;
      padding: 7.5% 0; } } }

@media screen and ( max-width: 480px) {
  .project {
    &__title {
      font-size: 7.5vw; }
    &__caption {
      font-size: 4vw; } } }
